import React from 'react'
import styled from 'styled-components'

const StyledDiffRed = styled.span`
color: red;
background-color: rgba(81, 22, 10, 1);
border-radius: 4px;
margin: 2px;
padding: 2px;
`
const StyledDiffGreen = styled.span`
color: green;
color: rgba(62, 212, 25, 1);
background-color: rgba(22, 85, 7, 1);
border-radius: 4px;
margin: 2px;
padding: 2px;
`

export default function Indicator(props) {

    let diff = props.today - props.before
    // console.log("diff = " + diff)

    if (isNaN(props.before)) {
        diff = 0
    }

    if (isNaN(props.today)) {
        return <p></p>
    }

    // diff === 0 ? <></> : diff > 0 ? < StyledDiffRed > {diff}</StyledDiffRed > : <StyledDiffGreen>{diff}</StyledDiffGreen>
    // console.log(diff)
    return (
        <p>
            {props.today + " "}
            {diff === 0 ? <></> : diff > 0 ? <StyledDiffRed>{"+" + diff}</StyledDiffRed> : <StyledDiffGreen>{diff}</StyledDiffGreen>}
        </p>
    )
}