import React from 'react'
import styled from 'styled-components'
import RaidFilter from '../../components/RaidFilter/RaidFilter'
import RaidList from '../../components/RaidList/RaidList'



import { useState } from 'react'

const HeaderContainer = styled.div`
background-color: white;
// background-color: rgb(207, 216, 255);
margin: 0;
padding: 20px 30px;
`
// const StyledH2 = styled.h2`
// margin: 0;
// padding: 0;
// `


export default function ListeVillage(props) {

    const [raidFilterToggle, setRaidFilterToggle] = useState(true);

    return (
        <HeaderContainer>
            {/* <StyledH2>Liste de villages</StyledH2> */}

            <RaidFilter
                setFilterChoice={props.setFilterChoice} X={props.filterChoice.X} Y={props.filterChoice.Y}
                raidFilterToggle={raidFilterToggle}
                setRaidFilterToggle={setRaidFilterToggle}
                handleChangeServer={props.handleChangeServer}
                handleChangeX={props.handleChangeX}
                handleChangeY={props.handleChangeY}
                server={props.server}
            />

            {Object.keys(props.filterChoice).length === 11 && !raidFilterToggle
                ?
                <RaidList
                    filterChoice={props.filterChoice}
                    dataDist={props.dataDist}
                    isLoadingAll={props.isLoadingAll}
                    errorAll={props.errorAll}

                />
                :
                <p>Error : filter empty</p>}

            {/* {console.log("ok"+Object.keys(filterChoice).length)} */}
            {/* {console.log(filterChoice)} */}
        </HeaderContainer>
    )
}
