import React from 'react'
import IndicatorPopVillage from './IndicatorPopVillage'
import IndicatorPopJoueur from './IndicatorPopJoueur'
import './TableauVillage.css'

export default function TableauVillage(props) {

    // console.log(props.data.isArray)
    if (!Array.isArray(props.data)) {
        // console.log(props.data)
        return <>Error : no data</>
    }
    // const all = []

    // props.data.forEach(village => {
    //     let
    // });

    // props.data.map((village) => {
    //     return ( <li key ={village.Aid}>village.An</li> )
    // })
    // console.log(props.data)

    const server5 = "ts7.x1.europe.travian.com"
    let numberStart = (props.page - 1) * props.townPerPage
    let numberEnd = props.page * props.townPerPage
    // console.log(numberStart, numberEnd)
    let arrayToRender = props.data.slice(numberStart, numberEnd)
    if (arrayToRender.length <= 0){
        props.handlePageChange(1)
    }

        return (

            <table>
                <thead>
                    <tr>
                        <th>Distance</th>
                        <th>X/Y</th>
                        <th>Village</th>
                        <th>j-4</th>
                        <th>j-3</th>
                        <th>j-2</th>
                        <th>j-1</th>
                        <th>j</th>
                        <th>Alliance</th>
                        <th>Joueur</th>
                    </tr>
                </thead>
                <tbody>
                    {arrayToRender.map((v) => {

                        let risk = 0
                        // console.log("===================")
                        for (let i = 0; i < 5 && i < v.Pop_Player.length; i++) {
                            // console.log(v.Un)
                            // console.log(i)
                            // console.log(v.Pop_Player)
                            // console.log(v.Pop_Player.length)
                            // console.log(v.Pop_Player[v.Pop_Player.length - i])
                            // console.log(v.Pop_Player[v.Pop_Player.length - i - 1])
                            let variationPopPlayer = v.Pop_Player[v.Pop_Player.length - i - 1] - v.Pop_Player[v.Pop_Player.length - i - 2]
                            if (variationPopPlayer > 0) {
                                risk += 1
                                if (variationPopPlayer >= 10) {
                                    risk += 1
                                }
                            }
                            // console.log(v.Pop)
                            let variationPop = v.Pop[v.Pop.length - i - 1] - v.Pop[v.Pop.length - i - 2]
                            if (variationPop > 0) {
                                risk += 1
                                if (variationPop > 15) {
                                    risk += 1
                                }
                                if (variationPop === variationPopPlayer && variationPop !== 0) {
                                    risk -= 1
                                }
                            }

                            if (variationPopPlayer > variationPop) {
                                risk += 1
                            }

                        }
                        if (v.Pop_Player > 2000) {
                            risk += 1
                            // console.log(v.distance + " 3")
                        }
                        // console.log(v)
                        // console.log(v.X + "_" + v.Y)
                        // console.log(v.X + "_" + v.Y + "_0")
                        return (<tr td key={v.X + "_" + v.Y + "_0"} className={risk === 0 ? "green" : risk <= 2 ? "orange" : risk === 3 ? "red" : "black"} >
                            <td key={v.X + "_" + v.Y + "_1"}>{v.distance} {risk} {"=>" + v.evol}</td>
                            <td key={v.X + "_" + v.Y + "_2"}>{"(" + v.X + "/" + v.Y + ")"}</td>
                            <td key={v.X + "_" + v.Y + "_3"}><a href={"https://" + server5 + "/position_details.php?x=" + v.X + "&y=" + v.Y} target="_blank" rel="noopener noreferrer">{v.Vn.replace(/'/g, '')}</a></td>
                            <td key={v.X + "_" + v.Y + "_4"}>{v.Pop[v.Pop.length - 5]}</td>
                            <td key={v.X + "_" + v.Y + "_5"}><IndicatorPopVillage today={v.Pop[v.Pop.length - 4]} before={v.Pop[v.Pop.length - 5]} /></td>
                            <td key={v.X + "_" + v.Y + "_6"}><IndicatorPopVillage today={v.Pop[v.Pop.length - 3]} before={v.Pop[v.Pop.length - 4]} /></td>
                            <td key={v.X + "_" + v.Y + "_7"}><IndicatorPopVillage today={v.Pop[v.Pop.length - 2]} before={v.Pop[v.Pop.length - 3]} /></td>
                            <td key={v.X + "_" + v.Y + "_8"}><IndicatorPopVillage today={v.Pop[v.Pop.length - 1]} before={v.Pop[v.Pop.length - 2]} /></td>
                            <td key={v.X + "_" + v.Y + "_9"}>{v.An}</td>
                            <td key={v.X + "_" + v.Y + "_10"}><a href={"https://" + server5 + "/profile/" + v.Uid} target="_blank" rel="noopener noreferrer">
                                <IndicatorPopJoueur village={v.Pop} joueur={v.Pop_Player} joueurName={v.Un} />
                            </a></td>
                            {/* <td key={v.X + "_" + v.Y + "_8"}><a href={"https://" + server5 + "/profile/" + v.Uid }> \&#8663</a></td> */}

                            {/* <th>{v.An}</th> */}
                            {/* <th>{v.An}</th> */}
                        </tr>)
                    })}
                </tbody>
            </table>
        )
}