import React from 'react';
import { useState, useEffect } from 'react';
import styled from 'styled-components';
import RangeSlider from '../Slider/RangeSlider';
import './RaidFilter.css';
import RaidFilterMini from './RaidFilterMini';
// import Slider from '@mui/material/Slider';
import Select from 'react-select';

const StyledForm = styled.form`
// background-color: purple;
// background-color: #ffbb00;
// background-color: #d08916;
// background-color: #d08916;
background-color: #c1cdc0;
// background: linear-gradient(180deg,#ffbb00 0%, #c6980e 100%);
// display: flex;
// flex-direction: column;
// justify-content: space-evenly;
// flex-wrap: wrap; 
column-count: 2;
column-gap: 5%;
column-rule: solid;
// column-fill: balance;
// border: 1px solid black;
// text-align: center;
`
const StyledFieldset = styled.fieldset`
// background-color: olive;
border: none;
border-top: solid;
display: flex;
flex-direction: column;
// justify-content: center;
// align-items: center;
// text-align: center;
column-break-inside:avoid;
`
// const StyledLegend = styled.legend`
// // display: flex;
// // flex-direction: column;
// `
const FormTitle = styled.h2`
column-span: all;
text-align: center;
`
const StyledButton = styled.button`
column-span: all;
text-align: center;
background-color: #df2f2f;
// display: block;
margin: auto;
margin-top: 20px;
margin-bottom: 20px;
`
const StyledTop = styled.div`
// background-color: red;
// display: flex;
// flex-direction: row;
`


export default function Filter(props) {

    const [range, setRange] = useState([0, 21])
    const [rangeTime, setRangeTime] = useState({ speed: 7, time: 3, pt_lvl: 0 })
    // const [rangeTime, setRangeTime] = useState({ speed: 10, time: 3, pt_lvl: 20 })
    const [targetType, setTargetType] = useState("joueur")
    const [popEvolution, setPopEvolution] = useState([0, 10])
    const [popEvolutionJour, setPopEvolutionJour] = useState(3)
    const [populationJoueur, setPopulationJoueur] = useState([0, 2000])
    const [populationVillage, setPopulationVillage] = useState([0, 1000])
    // const [hideIfTribe, setTribe] = useState([false, false, false, true])
    const [hideIfInAlliance, setHideIfInAlliance] = useState(false)
    const [hideIfInThisAlliance, setHideIfInThisAlliance] = useState([])

    // const checkSpeed = (e) => {
    //     setRangeTime({ speed: parseInt(e), time: rangeTime.time, pt_lvl: rangeTime.pt_lvl })
    // }

    const checkSpeed = (e) => {
        // let temp17 = range;
        // temp17.speed = parseInt(e)
        setRangeTime({ speed: parseInt(e), time: rangeTime.time, pt_lvl: rangeTime.pt_lvl })
        // setRangeTime(temp17)
    }

    const checkTime = (e) => {
        setRangeTime({ speed: rangeTime.speed, time: parseInt(e), pt_lvl: rangeTime.pt_lvl })
    }

    const checkPtLvl = (e) => {
        setRangeTime({ speed: rangeTime.speed, time: rangeTime.time, pt_lvl: parseInt(e) })
    }

    useEffect(() => {
        let dist = rangeTime.speed * rangeTime.time
        if (dist <= 20 || rangeTime.pt_lvl === 0) {
            setRange([range[0], rangeTime.speed * rangeTime.time])
        } else {
            let timeFirst20 = 20 / rangeTime.speed
            let time_pt = rangeTime.time - timeFirst20
            let distance_pt = time_pt * (rangeTime.speed * (1 + 0.2 * rangeTime.pt_lvl))
            let distance_finale = Math.round(20 + distance_pt)
            if (distance_finale > range) {
                if (distance_finale <= 250) {
                    setRange([range[0], distance_finale])
                    setUpdatedMax(distance_finale)
                } else {
                    setRange([range[0], 250])
                    setUpdatedMax(250)
                }
            }
            setRange([range[0], distance_finale])
        }
    }, [rangeTime])

    const [updatedMax, setUpdatedMax] = useState(100)
    const reperesRange = [{ value: 0, label: "0" },
    { value: 10, label: "10" },
    { value: 20, label: "20" },
    { value: 30, label: "30" },
    { value: 50, label: "50" },
    { value: 100, label: "100" }
    ]

    useEffect(() => {
        if (range[1] > 0.9 * updatedMax && updatedMax < 250) {
            setUpdatedMax(updatedMax + 5)
        }
        if (range[1] < 0.3 * updatedMax && updatedMax > 100) {
            setUpdatedMax(updatedMax - 5)
        }
        if (updatedMax < range[1] && updatedMax < 250) {
            setUpdatedMax(Math.min(range[1], 250))
        }
        if (updatedMax >= 200 || range[1] >= 200) {
            reperesRange.push({ value: 200, label: "200" })
        }
    }, [range])

    const handleRadioCible = (e) => {
        setTargetType(e.target.value)
    }

    const handleRadioAlly = (e) => {
        if (e.target.value === "true") {
            setHideIfInAlliance(true)
        } else {
            setHideIfInAlliance(false)
        }

    }

    const checkHideIfInThisAlliance = (e) => {
        setHideIfInThisAlliance(e)
    }

    const [negativPop, setNegativPop] = useState(true)

    const handleNegativPop = (e) => {
        setNegativPop(!negativPop)
    }

    const submitForm = (e) => {

        // console.log("clicked")
        e.preventDefault()

        let filterChoices = {
            X: props.X,
            Y: props.Y,
            range,
            targetType,
            popEvolution,
            popEvolutionJour,
            populationVillage,
            populationJoueur,
            // hideIfTribe,
            hideIfInAlliance,
            hideIfInThisAlliance,
            negativPop
        }

        props.setFilterChoice(filterChoices)
        props.setRaidFilterToggle(false)

        // console.log(filterChoices)
    }

    useEffect(() => {
        let filterChoices = {
            X: props.X,
            Y: props.Y,
            range,
            targetType,
            popEvolution,
            popEvolutionJour,
            populationVillage,
            populationJoueur,
            // hideIfTribe,
            hideIfInAlliance,
            hideIfInThisAlliance,
            negativPop
        }

        props.setFilterChoice(filterChoices)

        // console.log(filterChoices)
    }, [])

    const options = []
    props.server.list.forEach(srvr => {
        options.push({ value: srvr.value, label: srvr.label })
    })

    return (
        <div>
            {props.raidFilterToggle ?
                <StyledForm
                // onSubmit={e => submitForm(e)}
                >
                    <FormTitle>Filtre</FormTitle>

                    <div className="container_button_form">
                        <StyledButton
                            onClick={e => submitForm(e)}
                        >Let's go !
                        </StyledButton>
                    </div>

                    {/* <button className="btn_submit_filter"
                    onClick={e => submitForm(e)}
                >
                    Let's go ! (marche)</button> */}
                    <StyledTop>
                        {/* <p>{props.server.actif.label}</p> */}
                        <Select
                            options={options}
                            value={props.server.actif}
                            onChange={e => props.handleChangeServer(e)}
                        // defaultValue={{ label: "eu7", value: "eu7.travian.fr" }}
                        />
                        {/* <label>X */}
                            <input
                                type="number"
                                min="-200"
                                max="200"
                                value={props.X}
                                onChange={e => props.handleChangeX(e.target.value)}
                            />
                        {/* </label> */}
                        {/* <label>Y */}
                            <input
                                type="number"
                                min="-200"
                                max="200"
                                value={props.Y}
                                onChange={e => props.handleChangeY(e.target.value)}
                            />
                        {/* </label> */}
                        {/* <p>input origine : {props.X} / {props.Y}</p> */}
                    </StyledTop>

                    <StyledFieldset id="Rayon d'action">
                        <legend>Rayon d'action</legend>

                        <label>Distance (nbr de cases)
                            <RangeSlider
                                min={0}
                                max={updatedMax}
                                data={range}
                                setData={setRange}
                                reperes={reperesRange}
                            />
                        </label>

                        <p>OU temps</p>
                        <label>Vitesse (case/h)
                            <input
                                type="number"
                                min="3"
                                max="19"
                                value={rangeTime.speed}
                                onChange={e => checkSpeed(e.target.value)}
                            />
                        </label>
                        <label>Temps (h)
                            <input
                                type="number"
                                min="1"
                                max="20"
                                value={rangeTime.time}
                                onChange={e => checkTime(e.target.value)}
                            />
                        </label>
                        <label>PT (lvl)
                            <input
                                type="number"
                                min="0"
                                max="20"
                                value={rangeTime.pt_lvl}
                                onChange={e => checkPtLvl(e.target.value)}
                            />
                        </label>
                    </StyledFieldset>

                    <StyledFieldset id="Activité de la cible">
                        <legend>Activité de la cible</legend>

                        <p>Cible :</p>
                        <div>
                            <input type="radio" id="joueur" name="target" value="joueur"
                                checked={targetType === "joueur"}
                                onChange={handleRadioCible}
                            />
                            <label htmlFor="joueur">Joueur</label>

                            <input type="radio" id="village" name="target" value="village"
                                checked={targetType === "village"}
                                onChange={handleRadioCible}
                            />
                            <label htmlFor="village">Village</label>
                        </div>

                        <p>Evolution négative ?</p>
                        <label>oui
                            <input
                                type="checkbox"
                                checked={negativPop === true}
                                onChange={handleNegativPop}
                            />
                        </label>

                        <label>Evolution de la population de la cible :
                            <RangeSlider
                                min={0}
                                max={50}
                                data={popEvolution}
                                setData={setPopEvolution}
                                reperes={[{ value: 0, label: "0" },
                                { value: 10, label: "10" },
                                { value: 20, label: "20" },
                                { value: 30, label: "30" },
                                { value: 50, label: "ALL" }]}
                                // step={popEvolution[1] > 9 ? popEvolution[1] > 30 ? 40 : 5 : 1}
                                step={5}
                            />
                        </label>

                        <label>Depuis combien de jours :
                            <RangeSlider
                                min={1}
                                max={5}
                                data={popEvolutionJour}
                                setData={setPopEvolutionJour}
                                reperes={[{ value: 1, label: "1" },
                                { value: 2, label: "2" },
                                { value: 3, label: "3" },
                                { value: 4, label: "4" },
                                { value: 5, label: "5" }]}
                            />
                        </label>

                    </StyledFieldset>

                    <StyledFieldset id="Population de la cible">
                        <legend>Population</legend>

                        <label>Population Joueur
                            <RangeSlider
                                min={0}
                                max={10000}
                                data={populationJoueur}
                                setData={setPopulationJoueur}
                                reperes={[{ value: 0, label: "0" },
                                { value: 5000, label: "5000" },
                                { value: 10000, label: "10000" }]}
                                step={1000}
                            />
                        </label>

                        <label>Population Village
                            <RangeSlider
                                min={0}
                                max={1500}
                                data={populationVillage}
                                setData={setPopulationVillage}
                                reperes={[{ value: 0, label: "0" },
                                { value: 500, label: "500" },
                                { value: 1000, label: "1000" },
                                { value: 1500, label: "1500" }]}
                                step={100}
                            />
                        </label>

                    </StyledFieldset>

                    <StyledFieldset id="chase SI : liste options">
                        <legend>Enlève :</legend>
                        <ul>
                            <li>
                                <div>
                                    <p>Peuple</p>
                                    {/* <div>
                                    <input type="checkbox" id="scales" name="scales" />
                                    <label htmlFor="scales">Gaulois</label>
                                </div>

                                <div>
                                    <input type="checkbox" id="horns" name="horns" />
                                    <label htmlFor="horns">Romains</label>
                                </div>
                                <div>
                                    <input type="checkbox" id="horns" name="horns" />
                                    <label htmlFor="horns">Germains</label>
                                </div>
                                <div>
                                    <input type="checkbox" id="horns" name="horns" checked />
                                    <label htmlFor="horns">Natars</label>
                                </div> */}
                                </div>
                            </li>
                            {/* const [hideIfTribe, setTribe] = useState([false, false, false, true]) */}
                            <li>
                                <div>
                                    <p>Cache si dans une alliance ?</p>
                                    <div>
                                        <input type="radio" id="non" name="ally" value="false"
                                            checked={hideIfInAlliance === false}
                                            onChange={handleRadioAlly}
                                        />
                                        <label htmlFor="non">Non</label>

                                        <input type="radio" id="oui" name="ally" value="true"
                                            checked={hideIfInAlliance === true}
                                            onChange={handleRadioAlly}
                                        />
                                        <label htmlFor="oui">Oui</label>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <label>Cache cette alliance :
                                    <input
                                        type="text"
                                        value={hideIfInThisAlliance}
                                        onChange={e => checkHideIfInThisAlliance(e.target.value)}
                                    />
                                </label>

                            </li>
                        </ul>
                    </StyledFieldset>

                    {/* <StyledButton>Let's go !</StyledButton> */}

                </StyledForm>
                : <RaidFilterMini
                    setRaidFilterToggle={props.setRaidFilterToggle}
                    X={props.X}
                    Y={props.Y}
                />
            }
        </div>
    )
}
