import './App.css';
import MainContainer from './components/MainContainer/MainContainer';
import Header from './components/Header/Header';
import Footer from './components/Footer/footer';
import Accueil from './pages/Accueil/Accueil';
import ListeVillage from './pages/ListeVillage/ListeVillage';
import { BrowserRouter, Routes, Route } from 'react-router-dom'
import { useState } from 'react';
import { useFetch } from './utils/hooks/useFetch'


import { Loader } from './utils/style/Atoms.js'

function App() {

  let start = {
    X: 0,
    Y: 0,
    serverLabel: "ts2",
    serverValue: "ts2.travian.fr",
    launch_fetch: false
  }
  // console.log(start.launch_fetch)
  // console.log(localStorage.getItem('x'))
  // if (localStorage.getItem('x') !== 'undefined' && localStorage.getItem('x') !== null && localStorage.getItem('x') !== '') {
  if (localStorage.getItem('x') !== null) {
    // console.log(localStorage.getItem('x'))
    start.X = localStorage.getItem('x');
    start.launch_fetch = true;
  } else {
    start.launch_fetch = false
  }
  // console.log(start.launch_fetch)
  // if (localStorage.getItem('y') !== 'undefined' && localStorage.getItem('y') !== null && localStorage.getItem('y') !== '') {
  if (localStorage.getItem('y') !== null) {
    start.Y = localStorage.getItem('y');
    start.launch_fetch = true;
  } else {
    start.launch_fetch = false
  }
  // console.log(start.launch_fetch)
  // if (localStorage.getItem('serverLabel') !== 'undefined' && localStorage.getItem('serverLabel') !== null && localStorage.getItem('serverLabel') !== '') {
  if (localStorage.getItem('serverLabel') !== null) {
    start.serverLabel = localStorage.getItem('serverLabel')
    start.launch_fetch = true;
  } else {
    start.launch_fetch = false
  }
  // console.log(start.launch_fetch)
  // if (localStorage.getItem('serverValue') !== 'undefined' && localStorage.getItem('serverValue') !== null && localStorage.getItem('serverValue') !== '') {
  if (localStorage.getItem('serverValue') !== null) {
    start.serverValue = localStorage.getItem('serverValue')
    start.launch_fetch = true;
  } else {
    start.launch_fetch = false
  }

  // console.log(start.launch_fetch)


  // const [dataTown, setDataTown] = useState([])
  const [filterChoice, setFilterChoice] = useState({ X: start.X, Y: start.Y });
  const [server, setServer] = useState({
    list: [
      { label: "ts3", value: "ts3.travian.fr" },
      { label: "ts2", value: "ts2.travian.fr" },
      { label: "eu7", value: "eu7.travian.fr" }
    ],
    actif: { label: start.serverLabel, value: start.serverValue }
  })


  const handleChangeX = (e) => {
    setFilterChoice({
      ...filterChoice,
      X: e
    });
    localStorage.setItem('x', e)
  };

  const handleChangeY = (e) => {
    setFilterChoice({
      ...filterChoice,
      Y: e
    });
    localStorage.setItem('y', e)
  };

  const handleChangeServer = (e) => {
    setServer({
      ...server,
      actif: e
    });
    localStorage.setItem('serverLabel', e.label)
    localStorage.setItem('serverValue', e.value)
  };

  let dataDist = {
    coordonnees:
    {
      X: start.X,
      Y: start.Y
    },
    data_0_30: [],
    data_30_60: [],
    data_60_90: []
  }

  let dataToFetch = {
    data_0_30: false,
    data_30_60: false,
    data_60_90: false
  }

  // console.log("=================")
  // console.log(start.launch_fetch)
  if (start.launch_fetch) {

    // if (localStorage.getItem('data_0_30') !== 'undefined' && localStorage.getItem('data_0_30') !== null && localStorage.getItem('data_0_30') !== '') {
    if (localStorage.getItem('data_0_30') !== null) {
      dataDist.data_0_30 = JSON.parse(localStorage.getItem("data_0_30"))
    } else {
      // console.log("load 1")
      dataToFetch.data_0_30 = '/api/server/ts7.x1.europe.travian.com/town/' + 30 + "/" + start.X + "/" + start.Y
    }

    // if (localStorage.getItem('data_30_60') !== 'undefined' && localStorage.getItem('data_30_60') !== null && localStorage.getItem('data_30_60') !== '') {
    if (localStorage.getItem('data_30_60') !== null) {
      dataDist.data_30_60 = JSON.parse(localStorage.getItem("data_30_60"))
    } else {
      // console.log("load 2")
      dataToFetch.data_30_60 = '/api/server/ts7.x1.europe.travian.com/town/' + 60 + "/" + start.X + "/" + start.Y
    }

    // if (localStorage.getItem('data_60_90') !== 'undefined' && localStorage.getItem('data_60_90') !== null && localStorage.getItem('data_60_90') !== '') {
    if (localStorage.getItem('data_60_90') !== null) {
      dataDist.data_60_90 = JSON.parse(localStorage.getItem("data_60_90"))
    } else {
      // console.log("load 3")
      dataToFetch.data_60_90 = '/api/server/ts7.x1.europe.travian.com/town/' + 90 + "/" + start.X + "/" + start.Y
    }

  }

  let pack1 = useFetch(dataToFetch.data_0_30);

  let dataSaved1 = false
  if (!pack1.isLoading && !pack1.error && pack1.data && !dataSaved1) {
    localStorage.setItem("data_0_30", JSON.stringify(pack1.data))
    dataDist.data_0_30 = pack1.data
    dataSaved1 = true
    // console.log("storage 1")
  }
  let pack2 = useFetch(dataToFetch.data_30_60);

  let dataSaved2 = false
  if (!pack2.isLoading && !pack2.error && pack2.data && !dataSaved2) {
    localStorage.setItem("data_30_60", JSON.stringify(pack2.data))
    dataDist.data_30_60 = pack2.data
    dataSaved2 = true
    // console.log("storage 2")
  }

  let pack3 = useFetch(dataToFetch.data_60_90);

  let dataSaved3 = false
  if (!pack3.isLoading && !pack3.error && pack3.data && !dataSaved3) {
    localStorage.setItem("data_60_90", JSON.stringify(pack3.data))
    dataDist.data_60_90 = pack3.data
    dataSaved3 = true
    // console.log("storage 3")
  }

  let errorAll = false;
  if (pack1.error || pack2.error || pack3.error) {
    errorAll = true
  } else {
    errorAll = false
  }

  let isLoadingAll = true;
  if (!pack1.isLoading && !pack2.isLoading && !pack3.isLoading) {
    isLoadingAll = false
  }

  // if (!start.launch_fetch) {
  //   isLoadingAll = false
  // }
  // console.log("================")
  // console.log(isLoadingAll)
  // console.log(start.launch_fetch)


  if (
    localStorage.getItem("data_0_30") !== null &&
    localStorage.getItem("data_30_60") !== null &&
    localStorage.getItem("data_60_90") !== null) {
    // console.log("all seems ok, no need dl neither loading")
    start.launch_fetch = false
    isLoadingAll = false
  }

  return (
    <MainContainer>
      <BrowserRouter>
        <Header />
        {
          isLoadingAll && start.launch_fetch? (
            <Loader />
          ) :
            errorAll ?
              <p>error</p>
              :
              // <p>ok</p>
              null
        }
        {/* <h1>Accueil</h1> */}
        <Routes>

          <Route
            exact
            path="/"
            element={<Accueil
              handleChangeX={handleChangeX}
              handleChangeY={handleChangeY}
              filterChoice={filterChoice}
              setFilterChoice={setFilterChoice}
              server={server}
              handleChangeServer={handleChangeServer}
            />}
          />

          <Route
            exact
            path="/ListeVillage"
            element={<ListeVillage
              handleChangeX={handleChangeX}
              handleChangeY={handleChangeY}
              filterChoice={filterChoice}
              setFilterChoice={setFilterChoice}
              server={server}
              handleChangeServer={handleChangeServer}
              dataDist={dataDist}
              isLoadingAll={isLoadingAll}
              errorAll={errorAll}
            />}
          />

        </Routes>
      </BrowserRouter>
      <Footer />
    </MainContainer>
  );
}

export default App;
