import React from 'react'
import styled from 'styled-components';

const StyledForm = styled.form`
background-color: #c1cdc0;
text-align: center;
`
const StyledButton = styled.button`
background-color: green;
`

export default function RaidFilterMini(props) {

    const afficheFilter = (e) => {
        e.preventDefault()
        props.setRaidFilterToggle(true)
    }

    return (
        <StyledForm>
            <p>Voff : {props.X}/{props.Y}</p>
            <StyledButton
                onClick={e => afficheFilter(e)}
            >
                Filtre
            </StyledButton>
        </StyledForm>
    )
}
