import React from 'react'
import styled from 'styled-components'

import Select from 'react-select'

const MainContainer = styled.div`
background-color: white;
// background-color: rgb(207, 216, 255);
margin: 0;
padding: 20px 50px;
`

// const StyledH2 = styled.h2`
// margin: 0;
// padding: 0;
// `

export default function Accueil(props) {
    // console.log(props.server.list)
    // console.log(props.server.list[0])
    // console.log(props.server.list[1])
    // console.log(props.server.list[2])
    const options = []
    props.server.list.forEach(srvr => {
        options.push({ value: srvr.value, label: srvr.label })
        // console.log(srvr)
    })

    return (
        <MainContainer>
            <h2>Accueil</h2>
            <p>Outil de recherche d'inactifs. Pour commencer, choisir un serveur et entrer des coordonées :)</p>

            {/* <form> */}
            <p>{props.server.actif.label}</p>
            <Select
                options={options}
                value={props.server.actif}
                onChange={e => props.handleChangeServer(e)}
                defaultValue={{ label: "eu7", value: "eu7.travian.fr" }}
            />
            <label>X
                <input
                    type="number"
                    min="-200"
                    max="200"
                    value={props.filterChoice.X}
                    onChange={e => props.handleChangeX(e.target.value)}
                />
            </label>
            <label>Y
                <input
                    type="number"
                    min="-200"
                    max="200"
                    value={props.filterChoice.Y}
                    onChange={e => props.handleChangeY(e.target.value)}
                />
            </label>
            <p>input origine : {props.filterChoice.X} / {props.filterChoice.Y}</p>
            {/* </form> */}

            {/* const MyComponent = () => ( */}

            {/* ) */}

        </MainContainer>
    )
}
