import React from 'react'
import { useFetch } from '../../utils/hooks/useFetch'

import { Loader } from '../../utils/style/Atoms.js'

import { useState } from 'react'
// import { useParams } from 'react-router-dom'

import TableauVillage from '../TableauVillage/TableauVillage'

export default function RaidList(props) {

    // localStorage.setItem("data_60_90", JSON.stringify(pack3.data))


    // console.log(JSON.parse(localStorage.getItem("data_0_30")))
    // console.log(JSON.parse(localStorage.getItem("data_30_60")))
    // console.log(JSON.parse(localStorage.getItem("data_60_90")))

    // let JSON.parse(localStorage.getItem("data_0_30")))
    // console.log(JSON.parse(localStorage.getItem("data_30_60")))
    // console.log(JSON.parse(localStorage.getItem("data_60_90")))

    // console.log("=========DATA IMPORTED=========")

    let dataAll = props.dataDist
    let filter = props.filterChoice

    let isLoadingAll = props.isLoadingAll
    let errorAll = props.errorAll

    // console.log(dataAll)
    // console.log(filter)

    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    if (filter.X !== dataAll.coordonnees.X || filter.Y !== dataAll.coordonnees.Y || localStorage.getItem('data_0_30') === null) {
        localStorage.removeItem("data_0_30")
        localStorage.removeItem("data_30_60")
        localStorage.removeItem("data_60_90")
        console.log("different data, need new dl")
        console.log("double false")
        errorAll = false
        isLoadingAll = false
    }
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    let data1 = false;
    if (localStorage.getItem('data_0_30') === null ||
        parseInt(JSON.parse(localStorage.getItem("data_0_30"))[0].X) !== parseInt(filter.X) ||
        parseInt(JSON.parse(localStorage.getItem("data_0_30"))[0].Y) !== parseInt(filter.Y)
    ) {
        data1 = '/api/server/ts7.x1.europe.travian.com/town/' + 30 + "/" + filter.X + "/" + filter.Y
    }
    let pack1 = useFetch(data1);

    if (!pack1.isLoading && !pack1.error && pack1.data) {
        localStorage.setItem("data_0_30", JSON.stringify(pack1.data))
        dataAll.data_0_30 = pack1.data
    }
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    let data2 = false;
    if (localStorage.getItem('data_30_60') === null ||
        parseInt(JSON.parse(localStorage.getItem("data_30_60"))[0].X) !== parseInt(filter.X) ||
        parseInt(JSON.parse(localStorage.getItem("data_30_60"))[0].Y) !== parseInt(filter.Y)
    ) {
        data2 = '/api/server/ts7.x1.europe.travian.com/town/' + 60 + "/" + filter.X + "/" + filter.Y
    }
    let pack2 = useFetch(data2);

    if (!pack2.isLoading && !pack2.error && pack2.data) {
        localStorage.setItem("data_30_60", JSON.stringify(pack2.data))
        dataAll.data_30_60 = pack2.data
    }
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    let data3 = false;
    if (localStorage.getItem('data_60_90') === null ||
        parseInt(JSON.parse(localStorage.getItem("data_60_90"))[0].X) !== parseInt(filter.X) ||
        parseInt(JSON.parse(localStorage.getItem("data_60_90"))[0].Y) !== parseInt(filter.Y)
    ) {
        data3 = '/api/server/ts7.x1.europe.travian.com/town/' + 90 + "/" + filter.X + "/" + filter.Y
    }
    let pack3 = useFetch(data3);

    if (!pack3.isLoading && !pack3.error && pack3.data) {
        localStorage.setItem("data_60_90", JSON.stringify(pack3.data))
        dataAll.data_60_90 = pack3.data
    }
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    if (pack1.error || pack2.error || pack3.error) {
        errorAll = true
    } else {
        errorAll = false
    }
    //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // console.log(dataAll)
    // console.log(dataAll.data_0_30)
    // console.log(Array.isArray(dataAll.data_0_30))
    // console.log(dataAll.data_0_30)
    // console.log(dataAll.data_30_60)
    // console.log(dataAll.data_60_90)

    let dataMain = dataAll.data_0_30.concat(dataAll.data_30_60).concat(dataAll.data_60_90)

    // console.log(dataMain)
    // console.log(Array.isArray(dataMain))
    // console.log(dataMain.length)

    // const isPropValuesEqual = (subject, target, propNames) =>
    //     propNames.every(propName => subject[propName] === target[propName]);

    // const getUniqueItemsByProperties = (items, propNames) =>
    //     items.filter((item, index, array) =>
    //         index === array.findIndex(foundItem => isPropValuesEqual(foundItem, item, propNames))
    //     );

    // let printy = getUniqueItemsByProperties(dataMain, "_id")
    let printy = [...new Map(dataMain.map(v => [v._id, v])).values()]
    // console.log(printy.length)



    // if (typeof props.filterChoice === 'object' && props.filterChoice !== null) {
    //     return <p>error : filtre out</p>
    // }
    // let x = 110;
    // let y = -109;

    // let x = 110;
    // let y = -109;
    // x = props.filterChoice.X
    // y = props.filterChoice.Y

    // const params = "/" + 20 + "/" + x + "/" + y;

    // const { data, isLoading, error } = useFetch('http://localhost:3000/api/server/ts7.x1.europe.travian.com/town')
    // const { data, isLoading, error } = useFetch('http://localhost:3000/api/server/ts7.x1.europe.travian.com/town' + params)


    ////////////////////////////////////////////////////////////////////////////
    ////////////////////////////////////////////////////////////////////////////
    let data = dataMain
    data = printy

    let dataOk = [];
    // console.log(props.filterChoice)
    // let filter = props.filterChoice;

    // if (Array.isArray(data)) {
    //     data.forEach(village => {
    //         village.distance = Math.round(Math.pow(Math.pow((filter.X - parseInt(village.X)), 2) + Math.pow((filter.Y - parseInt(village.Y)), 2), 0.5) * 100, 2) / 100
    //     });
    // }


    const TriData = (array) => {
        dataOk = array.filter(village => {
            // console.log(village)
            // console.log(village.Pop)
            let keepVillage = true;

            // if (village.distance === 0) {
            //     console.log(village)
            // }

            ///////////////////////////////////////////////////////////////
            ///////////////////////////////////////////////////////////////RANGE
            if (village.distance < parseInt(filter.range[0]) ||
                village.distance > parseInt(filter.range[1])) {
                if (village.distance !== 0) {
                    keepVillage = false;
                }
            }
            ///////////////////////////////////////////////////////////////
            ///////////////////////////////////////////////////////////////CACHE SI dans UNE ally
            if (filter.hideIfInAlliance && village.An !== "") {
                keepVillage = false;
            }
            ///////////////////////////////////////////////////////////////
            ///////////////////////////////////////////////////////////////
            let popEvolPosi = 0;
            let popEvolAll = 0;
            let tempo;

            for (let j = 0; j < filter.popEvolutionJour; j++) {
                tempo = 0
                if (filter.targetType === "joueur") {
                    tempo = village.Pop_Player[village.Pop_Player.length - j - 1] - village.Pop_Player[village.Pop_Player.length - j - 2];
                } else if (filter.targetType === "village") {
                    tempo = village.Pop[village.Pop.length - 1] - village.Pop[village.Pop.length - parseInt(filter.popEvolutionJour) - 1];

                }
                if (tempo > 0) {
                    popEvolPosi += tempo
                }
                popEvolAll += tempo
            }
            village.evol = popEvolPosi

            // console.log(popEvol)
            // if (popEvol < parseInt(filter.popEvolution[0]) ||
            if (popEvolPosi < parseInt(filter.popEvolution[0]) && parseInt(filter.popEvolution[0]) !== 0 ||
                popEvolAll < parseInt(filter.popEvolution[0]) && parseInt(filter.popEvolution[0]) === 0 && !filter.negativPop ||
                popEvolPosi >= parseInt(filter.popEvolution[1])
            ) {
                keepVillage = false;
            }
            ///////////////////////////////////////////////////////////////
            ///////////////////////////////////////////////////////////////
            if (village.Pop[village.Pop.length - 1] < filter.populationVillage[0] ||
                village.Pop[village.Pop.length - 1] > filter.populationVillage[1]) {
                keepVillage = false;
            }
            ///////////////////////////////////////////////////////////////
            ///////////////////////////////////////////////////////////////
            if (village.Pop_Player[village.Pop_Player.length - 1] < filter.populationJoueur[0] ||
                village.Pop_Player[village.Pop_Player.length - 1] > filter.populationJoueur[1]) {
                keepVillage = false;
            }
            // console.log(keepVillage)
            ///////////////////////////////////////////////////////////////
            ///////////////////////////////////////////////////////////////
            ///////////////////////////////////////////////////////////////
            ///////////////////////////////////////////////////////////////
            if (keepVillage) {
                return true;
            }
            // if (village.distance === 0) {
            //     return true
            // }
            return false
        });

        dataOk.sort((a, b) => a.distance - b.distance)
        // console.log(dataOk[0])
    }

    if (Array.isArray(data) && !errorAll) {
        // console.log("all ok")
        // data2 = [data[0],
        // data[1],
        // data[2],
        // data[3],
        // data[4]
        // ]
        TriData(data);
    }

    //Function de tri des data

    // console.log("=================")
    // console.log("data totale")
    // console.log(data.length)
    // // console.log(data)

    // // console.log("data filtrée")
    // console.log(dataOk.length)
    // // console.log(dataOk)
    // console.log("----------------")
    // console.log(data)
    // console.log(errorAll)
    // console.log(pack1)
    // console.log(pack2)
    // console.log(pack3)
    // console.log(isLoadingAll)

    if (data === null || data === "") {
        errorAll = true
    }


    const [page, setPage] = useState(1)
    const [townPerPage, setTownPerPage] = useState(20)

    let numerotation = []
    let numberOfPage = Math.ceil(dataOk.length / townPerPage)

    const handlePageChange = (e) => {
        // console.log(e)
        setPage(e)
    }

    for (let k = 0; k < numberOfPage; k++) {
        numerotation.push(<button
            value={k + 1}
            onClick={e => handlePageChange(e.target.value)}
            key={k + 1 + "button"}
        >{k + 1}</button>)
    }

    const handleChangeTownPerPage = (e) => {
        // console.log(e)
        if (e >= 10 && e <= 100 && e % 10 === 0) {
            setTownPerPage(e)
        }

    }


    // console.log(numerotation)
    return (
        <div>
            <h3>Ici : liste de vivi selon le filtre</h3>

            {isLoadingAll ? (
                <Loader />
            ) : errorAll ?
                <p>ERROR</p> : (
                    <div>
                        <span>
                            {/* {console.log(data)} */}
                            <p>data chargée : {data.length} villages chargés / {dataOk.length} villages correspondants /</p>
                            <input
                                value={townPerPage}
                                type="number"
                                max="100"
                                min="10"
                                step="10"
                                onChange={e => handleChangeTownPerPage(e.target.value)}
                            ></input>

                        </span>

                        {numerotation}

                        <TableauVillage
                            data={dataOk.slice(0, 30000)}
                            page={page}
                            townPerPage={townPerPage}
                            handlePageChange={handlePageChange}
                        />
                        {/* <TableauVillage data={data2} /> */}
                        {/* <TableauVillage data={[{"Pop":[854,854],"Day":["25/11","27/11"],"_id":"ts7.x1.europe.travian.com_1","X":0,"Y":0,"Vn":"'Natars'","Uid":1},{"Pop":[238,238],"Day":["25/11","27/11"],"_id":"ts7.x1.europe.travian.com_10","X":6,"Y":3,"Vn":"'WW Village'","Uid":1},{"Pop":[653,657],"Day":["25/11","27/11"],"_id":"ts7.x1.europe.travian.com_100","X":-25,"Y":-22,"Vn":"'1. Medellín Cartel'","Uid":92}]}/> */}
                    </div>
                )}




        </div>


    )
}
