export default function IndicatorPopVillage(props) {

    // let diff = props.today - props.before
    // console.log("diff = " + diff)

    // if (isNaN(props.before)) {
    // diff = 0
    // }

    // if (isNaN(props.today)) {
    // return <p></p>
    // }

    let pop1 = props.joueur[props.joueur.length - 1];
    let pop2 = props.joueur[props.joueur.length - 2];
    let pop3 = props.joueur[props.joueur.length - 3];
    let pop4 = props.joueur[props.joueur.length - 4];

    let diff1 = pop1 - pop2;
    let diff2 = pop2 - pop3;
    let diff3 = pop3 - pop4;

    return (
        <p>
            {props.joueurName + " "}
            {/* {diff === 0 ? <></> : diff > 0 ? <StyledDiffRed>{"+" + diff}</StyledDiffRed> : <StyledDiffGreen>{diff}</StyledDiffGreen>} */}
            {/* {pop3 + "/" + pop2 + "(" + diff2 + ")/" + pop1 + "(" + diff1 + ")"} */}
            {diff3 + " / " + diff2 + " / " + diff1}
        </p>
    )
}